import React, { useState } from 'react';
import { Link } from 'react-router-dom';
// Services
import toast from '../../../../services/toast/toastService';
// Redux
import { useDispatch } from 'react-redux';
import { setUser } from '../../../../redux/slices/authSlice'; 
import { get as getMasterfiles } from '../../../../redux/async/masterfilesAsync';
import { setIsFirstLogin } from '../../../../redux/slices/systemSlice';
// Repository 
import authRepository from '../../../../repositories/authRepository';
// MUI Components  
import TextField from "@mui/material/TextField";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Paper from "@mui/material/Paper";
import Container from "@mui/material/Container";
import InputAdornment from "@mui/material/InputAdornment";
import IconButton from "@mui/material/IconButton";
import Grid from "@mui/material/Grid";
import LoadingButton from '@mui/lab/LoadingButton';

// react-icons
import { MdLogin, MdPerson, MdVisibility, MdVisibilityOff } from 'react-icons/md';

// Page Styling
import { makeStyles } from '@mui/styles';
import LoginCover from '../../../../../assets/login-cover.png'
import clsx from 'clsx';
import storage from '../../../../services/storage/storageService';

const useStyles = makeStyles((theme) => ({
    title: {
        fontWeight: "bold",
        marginTop: 10
    },
    paper: {
        padding: theme.spacing(4),
        margin: theme.spacing(0, 0, 0, 0),
        display: 'flex',
        flexDirection: 'column',
        justifyContent: "left",
        alignItems: 'left',
        height: '100%',
        maxHeight: '100%',
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(1, 0, 2, 0),
        color: '#ffffff'
    },
    loginCover: {
        backgroundColor: "transparent !important",
        backgroundImage: `url(${LoginCover})`,
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundRepeat: "no-repeat",
    }
}));

export default function Login(props) { 

    const dispatch = useDispatch();
    const classes = useStyles();

    const [loading, setLoading] = useState(false);

    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [isShowPassword, setIsShowPassword] = useState(false);
    const toggleIsShowPassword = () => setIsShowPassword(!isShowPassword);

    const handleLogin = async (e) => {
        e.preventDefault();
        try {
            setLoading(true);
            let postData = { 'email': email, 'password': password };

            let response = await authRepository.login(postData);
            
            let authData = response.data;  
            if (!response.error) {
                
                storage.setItem('_token', authData);
 
                dispatch(getMasterfiles(authData.token));     
                  
                if(authData.is_first_login === 1 && authData.role.id === 5 && authData.reg_type === "online"){            
                    storage.setItem('_showOnBoard', true);
                    storage.setItem('_first', authData.is_first_login);                    
                    dispatch(setIsFirstLogin(authData.is_first_login));
                } else{ 
                    storage.setItem('_showOnBoard', false);
                    storage.setItem('_first', 0);                    
                    dispatch(setIsFirstLogin(0));
                }

                dispatch(setUser(authData));                
            }else{ 
                toast.modalError(response.message);
            }

        } catch (e) {  
            toast.modalError('Unable to login');
        } finally {
            setLoading(false);
        }

    }
 
    return (
        <div style={{ height: "100%", width: "100%" }}>
            <Container component="main" sx={{ maxWidth: '1100px !important' }}>
                <Box sx={{ mt: 5, mb: 10 }}>
                    <Paper elevation={0} sx={{ minHeight: 500 }}>
                        <Grid container>
                            <Grid item xs={12} sm={6}>
                                <Paper square elevation={0} className={classes.paper}>
                                    <Box display="flex" flexDirection="row">
                                        <Typography sx={{ mt: 2 }} variant="h5" className={classes.title}>
                                            Login
                                        </Typography>
                                        <MdLogin size={25} style={{ marginTop: 15, marginLeft: 5 }} />
                                    </Box>
                                    <form onSubmit={(e) => handleLogin(e)} className={classes.form} autoComplete="off">
                                        <TextField
                                            onChange={(e) => { setEmail(e.target.value); }}
                                            variant="outlined"
                                            size="large"
                                            margin="normal"
                                            required
                                            fullWidth
                                            name="email"
                                            id="email"
                                            label="Email"
                                            placeholder="Email"
                                            autoFocus
                                            InputProps={{
                                                endAdornment: (
                                                    <InputAdornment position="end">
                                                        <MdPerson size={20} />
                                                    </InputAdornment>
                                                ),
                                            }}
                                        />
                                        <TextField
                                            type={isShowPassword ? "text" : "password"}
                                            onChange={(e) => { setPassword(e.target.value); }}
                                            variant="outlined"
                                            size="large"
                                            margin="normal"
                                            required
                                            fullWidth
                                            name="password"
                                            label="Password"
                                            placeholder="Password"
                                            id="password"
                                            InputProps={{
                                                endAdornment: (
                                                    <InputAdornment position="end">
                                                        <IconButton onClick={toggleIsShowPassword} size="small" sx={{ mr: -0.5 }}>
                                                            {isShowPassword ?
                                                                <MdVisibility size={20} />
                                                                :
                                                                <MdVisibilityOff size={20} />
                                                            }
                                                        </IconButton>
                                                    </InputAdornment>
                                                ),
                                            }}
                                        />
                                        <LoadingButton
                                            type="submit"
                                            fullWidth
                                            variant="contained"
                                            color="primary"
                                            className={classes.submit}
                                            loading={loading}
                                            sx={{ mt: 2 }}
                                            size="large"
                                        >
                                            Log In
                                        </LoadingButton>
                                        <Box textAlign="center" mt={2}>
                                            <Link color='#00BFA5' to={'/forgot'} variant="body1" underline="hover">
                                                Forgot password?
                                            </Link>
                                        </Box>
                                    </form>
                                </Paper>
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <Paper square elevation={0} className={clsx(classes.paper, classes.loginCover)}></Paper>
                            </Grid>
                        </Grid>
                    </Paper>
                </Box>
            </Container>
        </div>
    );
}